


















import Component from 'vue-class-component';
import VueBase from '@/utils/VueBase';
import { dataManager } from '@/utils/DataManager';
import { CharacterType } from '@/store/characters/charactersFilter';

@Component({
  components: {
  },
})
export default class extends VueBase {
  public get dataManager() {
    return dataManager;
  }

  public get characterTypeFilter() {
    return [
      {
        label: this.$t('戦闘キャラクター'),
        value: CharacterType.battle,
      },
      {
        label: this.$t('NPC'),
        value: CharacterType.npc,
      },
    ];
  }

  public get sortOptions() {
    return [
      {
        label: this.$t('古い'),
        value: 0,
      },
      {
        label: this.$t('新しい'),
        value: 1,
      },
    ];
  }

  get characterType() {
    return this.$store.state.charactersFilter.characterType;
  }

  set characterType(value) {
    this.$store.commit('charactersFilter/setCharacterType', value);
  }

  public get sort() {
    return this.$store.state.charactersFilter.sort;
  }

  public set sort(value) {
    this.$store.commit('charactersFilter/setSort', value);
  }

  public beforeMount() {
    if (typeof this.$route.query.characterType !== 'undefined') {
      this.characterType = +this.$route.query.characterType;
    }
  }

  public get filteredCharacters() {
    if (this.sort === 1) {
      return [...this.characters].reverse();
    }
    return this.characters;
  }

  public get characters() {
    switch (this.characterType) {
      case CharacterType.battle:
        return dataManager.charactersCanBattle;
      case CharacterType.npc:
        return dataManager.characterNpcs;
      case CharacterType.none:
      default:
        return dataManager.chara.m_vList;
    }
  }
}
